import React, { useState, useEffect } from "react";
import { useSearchParams, useNavigate } from 'react-router-dom';
import styles from './Community.module.css';

function Community() {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const defaultTab = searchParams.get('tab') || 'notice';
  const [category, setCategory] = useState(defaultTab);

  useEffect(() => {
    const newTab = searchParams.get('tab');
    if (newTab && newTab !== category) {
      setCategory(newTab);
    }
  }, [searchParams, category]);

  const noticePosts = [
    { title: "  ", date: "2025.06.01" },
    { title: "   ", date: "2025.05.25" },
    { title: "  ", date: "2025.05.10" },
  ];
  const boardPosts = [
    { title: "   ", date: "2025.04.01" },
    { title: "  ", date: "2025.03.28" },
    { title: "   ", date: "2025.03.25" },
    { title: "  ", date: "2025.03.20" },
  ];
  const suggestionPosts = [
    { title: "   ", date: "2025.04.03" },
    { title: "    ", date: "2025.03.30" },
    { title: "   ", date: "2025.03.27" },
    { title: "  ", date: "2025.03.22" },
  ];
  const cardNewsPosts = [
    { title: "  ", date: "2025.04.15" },
    { title: "  ", date: "2025.04.10" },
    { title: "   ", date: "2025.04.05" },
  ];

  const tabs = [
    { key: "notice", label: "", description: " " },
    { key: "board", label: "", description: " " },
    { key: "suggestion", label: "", description: " " },
    { key: "cardNews", label: "", description: " " },
  ];

  const currentPosts = {
    notice: noticePosts,
    board: boardPosts,
    suggestion: suggestionPosts,
    cardNews: cardNewsPosts,
  }[category];

  const currentDescription = {
    notice: " ",
    board: "   ",
    suggestion: "     ",
    cardNews: "     ",
  }[category];

  const currentCategoryInfo = {
    notice: { category: "", since: "2025" },
    board: { category: "", since: "2025" },
    suggestion: { category: "", since: "2025" },
    cardNews: { category: "", since: "2025" },
  }[category];

  return (
    <div className={styles.container}>
      <div className={styles.mainContent}>
        <div className={styles.sidebar}>
          <div className={styles.buttonContainer}>
            {tabs.map((tab) => (
              <button
                key={tab.key}
                className={`${styles.categoryBtn} ${category === tab.key ? styles.categoryBtnActive : ""}`}
                onClick={() => {
                  setCategory(tab.key);
                  navigate(`/community?tab=${tab.key}`);
                }}
              >
                <h3>{tab.label}</h3>
                <p>{tab.description}</p>
              </button>
            ))}
          </div>
        </div>
        <div className={styles.contentContainer}>
          <div className={`${styles.content} ${styles.contentActive}`}>
            <h2>{tabs.find(t => t.key === category)?.label}</h2>
            <p className={styles.contentDescription}>{currentDescription}</p>
            <div className={styles.categoryInfo}>
              <p>Category: {currentCategoryInfo.category}</p>
              <p>Since: {currentCategoryInfo.since}</p>
            </div>
            <div className={styles.buttonGroup}>
              <button className={styles.actionBtn}></button>
              {category === "cardNews" ? (
                <>
                  <button className={styles.actionBtn}></button>
                  <button className={styles.actionBtn}></button>
                </>
              ) : (
                <>
                  <button className={styles.actionBtn}></button>
                  {category === "board" && (
                    <>
                      <button className={styles.actionBtn}></button>
                      <button className={styles.actionBtn}></button>
                    </>
                  )}
                </>
              )}
            </div>
            <ul className={styles.postList}>
              {currentPosts.map((post, idx) => (
                <li className={styles.postItem} key={idx}>
                  <span className={styles.postTitle}>{post.title}</span>
                  <span className={styles.postDate}>{post.date}</span>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Community;
