import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./Signup.css";


function Signup() {
  const nav = useNavigate();
  const [showPassword, setShowPassword] = useState(false);
  const [formData, setFormData] = useState({
    id: "",
    password: "",
    password_confirm: "",
    name: "",
    gender: "",
    nickname: "",
    birth: "",
    phone: "",
    email: "",
  });

  const [idChecked, setIdChecked] = useState(false);
  const checkIdDuplicate = () => {
    const savedUser = sessionStorage.getItem("user");
    const savedId = savedUser ? JSON.parse(savedUser).id : null;
    if (formData.id === savedId) {
      alert("  .");
    } else {
      alert("  .");
      setIdChecked(true);
    }
  };
  const onChangeForm = (e) => {
    const { name, value, type, checked } = e.target;

    if (name === "gender") {
      setFormData({ ...formData, [name]: value }); //   
    } else {
      setFormData({ ...formData, [name]: type === "checkbox" ? checked : value });
    }

    //     
    if (name === "id") {
      setIdChecked(false);
    }
  };

  const onSubmitForm = (e) => {
    e.preventDefault();

    const { id, password, password_check } = formData;

    if (!id) return alert(" ");
    if (!idChecked) return alert("   ");
    if (!password) return alert(" ");
    if (!password_check) return alert("  ");
    if (password !== password_check) return alert("   .");

    sessionStorage.setItem("user", JSON.stringify(formData));
    alert(" !");
    nav("/login");
  };
  return (
    <div className="signup-container">
      <form className="form-submit" onSubmit={onSubmitForm}>
        <div className="input-box">
          <i className="icon user" />
          <input
            className="id"
            type="text"
            placeholder=""
            onChange={onChangeForm} />
          <button type="button" className="idChecked" onClick={checkIdDuplicate} >
            
          </button>
        </div>
        <div className="input-box">
          <i className="icon lock" />
          <input
            className="password"
            type={showPassword ? "text" : "password"}
            placeholder=""
            onSubmit={onChangeForm}
          />
          <i
            className={`icon eye ${showPassword ? "" : "closed"}`}
            onClick={() => setShowPassword(!showPassword)}
          />
        </div>
        <div className="input-box">
          <i className="icon lock" />
          <input
          type="password"
            className="password_check"
            placeholder=""
            onSubmit={onChangeForm}
          />
        </div>
        <div className="input-box">
          <i className="icon email" />
          <input

            type="email"
            placeholder=""
            onSubmit={onChangeForm} />
        </div>
        <div className="input-box">
          <i className="icon user" />
          <input
            type="text"
            placeholder=""
            onSubmit={onChangeForm}
          />
        </div>
        <div className="input-box">
          <i className="icon user" />
          <input
            type="text"
            placeholder=""
            onSubmit={onChangeForm}
          />
        </div>
        <div className="input-box">
          <i className="icon calendar" />
          <input
            type="text"
            placeholder=" 8"
            onSubmit={onChangeForm}
          />
        </div>
        <div className="select-box">
          <i className="icon antenna" />
          <select>
            <option> </option>
            <option>SKT</option>
            <option>KT</option>
            <option>LG U+</option>
          </select>
        </div>
        <div className="input-box">
          <i className="icon phone" />
          <input
            type="text"
            placeholder=""
            onSubmit={onChangeForm}
          />
        </div>
        <div className="radio-group">
          <label><input type="radio" name="gender" /> </label>
          <label><input type="radio" name="gender" /> </label>
          <label><input type="radio" name="nationality" /> </label>
          <label><input type="radio" name="nationality" /> </label>
        </div>
        <div className="submit-button">
          <button type="submit"></button>
        </div>
      </form>
    </div>
  );
}

export default Signup;