import React, { useState, useEffect } from 'react';
import './Mypage.css';
import { useNavigate } from 'react-router-dom';

function Counseling() {
  const nav = useNavigate();
  const [data, setData] = useState([]);
  const [detailItem, setDetailItem] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 15;

  useEffect(() => {
    fetch('/api/counseling') //  API 
      .then(response => {
        if (!response.ok) {
          throw new Error('  ');
        }
        return response.json();
      })
      .then(json => setData(json))
      .catch(err => {
        console.error('  :', err);
      });
  }, []);

  const fetchDetail = (id) => {
    fetch(`/api/counselings/${id}`)
      .then(res => {
        if (!res.ok) throw new Error('   ');
        return res.json();
      })
      .then(data => setDetailItem(data))
      .catch(err => console.error(err));
  };

  //   
  if (detailItem === null) {
    return <div className="tab-content active">   ...</div>;
  }

  if (fetchDetail) {
    return (
      <div className="tab-content active">
        <h3>{detailItem.title}</h3>
        <p><strong>:</strong> {detailItem.date}</p>
        <p><strong>:</strong><br />{detailItem.content}</p>
        {/* <button className="back-button" onClick={() => setDetailItem(null)}> </button> */}
        <button className="back-button" type="button" onClick={() => {nav(-1);}}>
           
        </button>
      </div>
    );
  }

  const totalPages = Math.ceil(data.length / itemsPerPage);
  const pageData = data.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  return (
    <div className="tab-content active">
      <table>
        <thead>
          <tr><th></th><th></th></tr>
        </thead>
        <tbody>
          {pageData.map(d => (
            <tr key={d.id} className="clickable-row" onClick={() => setDetailItem(d)}>
              <td>{d.title}</td><td>{d.date}</td>
            </tr>
          ))}
        </tbody>
      </table>
      <div className="pagination">
        {Array.from({ length: totalPages }, (_, i) => (
          <button
            key={i}
            className={currentPage === i + 1 ? 'active' : ''}
            onClick={() => setCurrentPage(i + 1)}
          >
            {i + 1}
          </button>
        ))}
      </div>
    </div>
  );
}

export default Counseling;