import React, { useState } from "react";
import ChatMessage from "./ChatMessage";
import ChatInput from "./ChatInput";
import "./Chatbot.css";
import axios from "axios";

export default function Chatbot() {
  const [messages, setMessages] = useState([
    { from: "bot", text: "  ." },
  ]);
  const [loading, setLoading] = useState(false);

  const handleSend = async (text) => {
    const newMessages = [...messages, { from: "user", text }];
    setMessages(newMessages);
    setLoading(true);

    try {
      const response = await axios.post("http://localhost:5001/api/chat", {
        message: text,
      });
      const data = response.data;
      if (data.text) {
        setMessages([...newMessages, { from: "bot", text: data.text }]);
      } else {
        setMessages([
          ...newMessages,
          { from: "bot", text: data.error || "  " }
        ]);
      }
    } catch (err) {
      setMessages([
        ...newMessages,
        { from: "bot", text: "  " }
      ]);
    }
    setLoading(false);

    axios.post("http://localhost:8080/api/phq9/predict", { text })
      .catch(() => { console.log("post ")});
      //   api    ->     
  };

  return (
    <div className="chatbot-container">
      <div className="messages">
        {messages.map((msg, idx) => (
          <ChatMessage key={idx} from={msg.from} text={msg.text} />
        ))}
        {loading && <ChatMessage from="bot" text="   ..." />}
      </div>
      <ChatInput onSend={handleSend} />
    </div>
  );
}
