import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from '../../api/axios';  
import './LoginExtraInfo.css';

function LoginExtraInfo() {
  const [gender, setGender] = useState('');
  const [birth, setBirth] = useState('');
  const [phone, setPhone] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      console.log(" :", { gender, birth, phone });

      const userEmail = localStorage.getItem('userEmail');

      //    
      await axios.post('/api/users/update-info', {
        userGender: gender,
        userBirth: birth,
        userPhone: phone
      });

      alert('   ');
      navigate('/');
    } catch (error) {
      console.error('   :', error.response ? error.response.data : error.message);
      alert('   ');
    }
  };

  return (
    <div className="login-extra-info-container">
      <h2 className="login-extra-info-title">  </h2>
      <form className="login-extra-info-form" onSubmit={handleSubmit}>
        <select
          value={gender}
          onChange={(e) => setGender(e.target.value)}
          required
        >
          <option value=""> </option>
          <option value="male"></option>
          <option value="female"></option>
        </select>
        <input
          type="text"
          placeholder=" (: 19980101)"
          value={birth}
          onChange={(e) => setBirth(e.target.value)}
          required
        />
        <input
          type="tel"
          placeholder=" (: 01012345678)"
          value={phone}
          onChange={(e) => setPhone(e.target.value)}
          required
        />
        <button type="submit"></button>
      </form>
    </div>
  );
}

export default LoginExtraInfo;
