import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./Navbar.css";
import logoImg from "../../Images/logo.png";

function Navbar() {
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const navigate = useNavigate();

  const token = localStorage.getItem('token'); 

  const handleLogout = () => {
    localStorage.removeItem('token');
    alert(".");
    navigate('/login'); 
  };

  const menuItems = [
    "",
    "",
    "",
    "",
    "",
    token ? "" : "" 
  ];

  const dropdownSections = [
    [
      { text: " ", to: "/chat" },
      { text: " ", to: "/chat" },
    ],
    [
      { text: "", to: "/diagnosis" },
    ],
    [
      { text: "", to: "/hospital_information" },
      { text: "/", to: "/location_and_pharmacies" },
    ],
    [
      { text: "", to: "/community?tab=notice" },
      { text: "", to: "/community?tab=board" },
      { text: "", to: "/community?tab=suggestion" },
      { text: "", to: "/community?tab=cardNews" },
    ],
    [
      { text: "   ", to: "/mypage?tab=counseling" },
      { text: "  ", to: "/mypage?tab=self_test" },
      { text: "", to: "/mypage?tab=emotion_chart" },
      { text: " ", to: "/mypage?tab=edit_profile" },
    ],
    token
      ? [
          { text: "", to: "#", onClick: handleLogout }
        ]
      : [
          { text: "", to: "/login" },
          { text: "", to: "/signup" },
        ]
  ];

  return (
    <div className="nav-bg">
      <div className="navbar-container">
        <div className="logo-area">
          <Link to="/">
            <img src={logoImg} alt=" " />
          </Link>
        </div>
        <div
          className="menu-center-wrapper"
          onMouseEnter={() => setDropdownOpen(true)}
          onMouseLeave={() => setDropdownOpen(false)}
        >
          <ul className="nav-menu">
            {menuItems.map((item, idx) => (
              <li key={item} className="nav-menu-item">
                <span
                  onClick={() => {
                    if (item === "") handleLogout();
                  }}
                >
                  {item}
                </span>
              </li>
            ))}
          </ul>
          {dropdownOpen && (
            <div className="dropdown-bg">
              <div className="dropdown-placeholder" />
              <div className="dropdown-menu">
                {dropdownSections.map((section, idx) => (
                  <div className="dropdown-section" key={idx}>
                    {section.map((btn) => (
                      btn.onClick ? (
                        <span
                          key={btn.text}
                          className="dropdown-btn"
                          onClick={btn.onClick}
                          style={{ cursor: 'pointer' }}
                        >
                          {btn.text}
                        </span>
                      ) : (
                        <Link
                          to={btn.to}
                          className="dropdown-btn"
                          key={btn.text}
                        >
                          {btn.text}
                        </Link>
                      )
                    ))}
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Navbar;
