import React, { useState } from 'react';
import "./Diagnosis.css";

const questions = [
  '1.     .',
  '2.      .',
  '3.    .     .',
  '4.     .',
  '5.  .    .',
  '6.        .',
  '7.   TV       .',
    [
    '8.         .',
    '\u00A0\u00A0\u00A0\u00A0        .'
    ],
  '9.     ,    .',
];

const options = [
  ' ',
  '',
  '',
  '',
  ' '
];

function Diagnosis() {
  const [answers, setAnswers] = useState(Array(questions.length).fill(null));

  const handleChange = (index, value) => {
    const newAnswers = [...answers];
    newAnswers[index] = value;
    setAnswers(newAnswers);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    alert(' .');
  };

  return (
    <div className="diagnosis-container">
      <h2 className="diagnosis-title"> </h2>
      <form onSubmit={handleSubmit}>
        {questions.map((question, index) => (
          <div className="diagnosis-question-block" key={index}>
            <div className="diagnosis-question">
              {Array.isArray(question)
                ? question.map((line, i) => (
                    <React.Fragment key={i}>
                      {line}
                      {i !== question.length - 1 && <br />}
                    </React.Fragment>
                  ))
                : question}
            </div>
            <div className="diagnosis-options">
              {options.map((option, i) => (
                <label className="diagnosis-radio" key={i}>
                  <input
                    type="radio"
                    name={`question-${index}`}
                    value={option}
                    checked={answers[index] === option}
                    onChange={() => handleChange(index, option)}
                    required
                  />
                  {option}
                </label>
              ))}
            </div>
          </div>
        ))}
        <button className="diagnosis-submit-btn" type="submit">
          
        </button>
      </form>
    </div>
  );
}

export default Diagnosis;
