import React, { useState } from 'react';
import './Login.css';
import { Link, useNavigate } from 'react-router-dom';
import axios from '../../api/axios';
import kakaoLoginImg from '../../Images/kakao_login.png'; 

function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();  //     

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('/api/users/login', {
        userEmail: email,
        password: password
      });
      console.log(" :", response.data);
      localStorage.setItem('token', response.data.token);
      alert(' !');
      //   
      navigate('/');
    } catch (error) {
      console.error(" :", error.response ? error.response.data : error.message);
      alert(' .    .');
    }
  };

  const handleKakaoLogin = () => {
    window.location.href = '/api/oauth/kakao/login';
  };

  return (
    <div className="login-container">
      <form className="login-form" onSubmit={handleSubmit}>
        <h2></h2>
        <input
          type="email"
          placeholder=""
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />
        <input
          type="password"
          placeholder=""
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
        />
        <button id="login" type="submit"></button>
        <button id="kakao-login" type="button" onClick={handleKakaoLogin}>
          <img src={kakaoLoginImg} alt=" " />
        </button>
        <Link to="/join">
          <button id="join" type="button"></button>
        </Link>
      </form>
    </div>
  );
}

export default Login;
