import React from "react";
import { Link } from "react-router-dom"; // 
import "./Main.css";
import b1Image from '../../Images/b1.png';
import b2Image from '../../Images/b2.png';
import b3Image from '../../Images/b3.png';
import b4Image from '../../Images/b4.png';

function Main() {
  return (
    <div className="container">
      <div className="wrap_box">
        <div className="box">
          <Link to="/chat">
            <div className="icon">
              <img src={b1Image} alt="" />
            </div>
            <p></p>
            <span className="arrow"></span>
          </Link>
        </div>
        <div className="box">
          <Link to="/diagnosis">
          <div className="icon">
            <img src={b2Image} alt="" />
          </div>
          <p></p>
          <span className="arrow"></span>
        </Link>
        </div>
        <div className="box">
          <div className="icon">
            <img src={b3Image} alt="" />
          </div>
          <p>/</p>
          <span className="arrow"></span>
        </div>
        <div className="box">
          <Link to='/community'>
          <div className="icon">
            <img src={b4Image} alt="" />
          </div>
          <p></p>
          <span className="arrow"></span>
          </Link>
        </div>
        <div className="box">
          <div className="icon">
            <img src={b1Image} alt="" />
          </div>
          <p></p>
          <span className="arrow"></span>
        </div>
        <div className="box">
          <div className="icon">
            <img src={b2Image} alt="" />
          </div>
          <p></p>
          <span className="arrow"></span>
        </div>
      </div>
    </div>
  );
}

export default Main;
